            }
        }
        select{
            border: 0px;
            border: 1px solid lighten( @light-border-color, 12%);
            padding-left: 2px;
            margin-bottom: 24px;
            padding:6px 12px;
            .round(0);
            .drop-shadow(0,0,0,0);
            .inner-shadow(0,0,0,0);
            .transition( 0.3s);
            &:focus{
                .drop-shadow(0,0,0,0);
                .inner-shadow(0,0,0,0);
                border: 1px solid @light-border-color;
            }
        }
        .select-style{
