.single-recent-post {
    
    margin-bottom: 26px;
    overflow: hidden;
    
    a {
        color: @default-font-color;
        margin-bottom: 8px;
        display: block;
        &:hover{
            color: @primary-color;
        }
        
    }
    
    span {
        font-size: 12px;
        display: block;
        color: @primary-color;
    }
    
}

